<!-- 预案信息 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">预案信息</p>
      </van-col>
    </van-row>
    <!-- 搜索框 -->
    <van-row>
      <van-form ref="form"
                v-model="form">
        <van-col span="20">
          <van-field v-model="form.PlanName"
                     class="dt-input"
                     :left-icon="require('@/assets/img/搜索.png')"
                     placeholder="请输入名称" />
        </van-col>
        <van-col span="4">
          <van-button native-type="submit"
                      class="dt-button"
                      @click="submit">搜索
          </van-button>
        </van-col>
      </van-form>
    </van-row>
    <!-- 列表 -->
    <van-row class="dt-content">
      <van-row>
        <van-list v-model="loading"
                  :finished="finished"
                  finished-text="没有更多了"
                  :error.sync="listError"
                  error-text="请求失败，点击重新加载">
          <van-row v-for="item in orderList"
                   :key="item.id"
                   @click="toInformation(item)"
                   class="dt-card">
            <myCard :image="'https://tse4-mm.cn.bing.net/th/id/OIP._SITyJkNY2j60N85kUduIAHaFj?pid=Api&rs=1'"
                    :title="item.PlanName"
                    :content="item.PlanContent"></myCard>
          </van-row>
        </van-list>
      </van-row>
    </van-row>
  </van-row>
</template>


<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  planInformationSearch //预案查询
} from '@/api/planInformation';
import myCard from "@/components/listCard/card";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    myCard,
  },
  data () {
    //这里存放数据
    return {
      form: {
        // OrganizationId: '',//所属景区
        // PlanCode: '',//预案编号
        // PlanType: '',//预案类型
        PlanName: '',//预案名称	
        // PlanModule: '',//预案模块	
        // PlanSubModule: '',// 预案子模块	
        // Enable: '',//1.启用，0.不启用	
        // PlanContent: '',////预案内容
        PageSize: 20,//条数	
        // PageIndex: 1,//页码	
      },
      orderList: [],
      loading: false, // 列表加载状态
      finished: false, // 列表加载完成
      listError: false, // 列表加载失败
    }
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getplanInformation()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //信息查询
    getplanInformation () {
      planInformationSearch(this.form).then(response => {
        if (response.Data.ContentList) {
          this.orderList = response.Data.ContentList
        } else {
          this.orderList = []
        }
        if (this.orderList.length >= this.totalCount) {
          this.finished = true;
        }
        this.loading = false
      })
    },
    //跳转预案信息详情页面
    toInformation (item) {
      this.$router.push({ name: 'DetailsOfPlanInformation', query: { ID: item.ID } })
    },
    submit () {
      this.orderList = []
      this.getplanInformation()
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  }
}
</script>
<style lang="scss" scoped>
@import '../../../styles/dimens';
//@import url(); 引入公共css类
$thirdHeight: px2rem(50);
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-input {
  padding: px2rem(2) px2rem(6);
  margin: px2rem(5) px2rem(12);
  height: px2rem(30);
  width: px2rem(300);
  background: #edf1f5;
  border-radius: px2rem(5);
}
.dt-button {
  width: px2rem(53);
  height: px2rem(30);
  margin-top: px2rem(5);
  padding: 0 0;
  background: #5870f8;
  border-radius: px2rem(5);
  font-size: px2rem(15);
  font-family: Source Han Sans CN;
  font-weight: 500;
  color: #ffffff;
}
.dt-content {
  height: calc(
    100% - #{$headerHeight} - #{$statusBarHeight} - #{$inputSearchHeight} - #{$inputSearchHeightMargin}
  );
  overflow-y: auto;
  background: #f6f8fa;
  .dt-card {
    margin: px2rem(14) px2rem(12);
    background: #ffffff;
    border-radius: px2rem(5);
  }
}
/deep/.van-field__body {
  height: 100%;
}
</style>
